import type { Player } from "@/types/player";
import CardDialog from "./CardDialog";
import { Button } from "./ui/button";
import { cn } from "@/lib/utils"; // Assuming cn utility is available
import SubstituteDialog from "./SubtituteDialog";

interface Props {
    className?: string;
    players: Player[];
    onScoreIncrement?: () => void;
    onScoreDecrement?: () => void;
    onTOincrement?: () => void;
    onYellowCard: (playerId: string) => void;
    onRedCard: (playerId: string) => void;
    onSubstitute: (playerOutId: string, playerInId: string) => void
    disabled: boolean;
}

function EditButtons({
    className = "",
    players,
    onScoreIncrement,
    onScoreDecrement,
    onTOincrement,
    onYellowCard,
    onRedCard,
    onSubstitute,
    disabled,
}: Props) {
    return (
        <section
            className={cn("bg-primary flex flex-col gap-10 pt-20", className)}
        >
            <Button
                disabled={disabled}
                className="edit-button"
                onClick={onScoreIncrement}
            >
                + bod
            </Button>
            <Button
                disabled={disabled}
                className="edit-button"
                onClick={onScoreDecrement}
            >
                - bod
            </Button>
            <Button
                disabled={disabled}
                className="edit-button"
                onClick={onTOincrement}
            >
                Time-out
            </Button>
            <SubstituteDialog
                disabled={disabled}
                players={players}
                buttonText="Stdn"
                buttonClass="edit-button"
                onSubstitute={onSubstitute}
            />
            <CardDialog
                disabled={disabled}
                card="lut"
                players={players}
                buttonText="lut karta"
                buttonClass="edit-button"
                onCardGiven={onYellowCard}
            />
            <CardDialog
                disabled={disabled}
                card="erven"
                players={players}
                buttonText="erven karta"
                buttonClass="edit-button"
                onCardGiven={onRedCard}
            />
        </section>
    );
}

export default EditButtons;
